@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.layout{height:calc(9 / 16 * 100vw);width:100vw}
.button{font-size:16px;padding:16px 67px;cursor:pointer;transition:background-color .3s,color .3s;text-transform:uppercase}@media screen and (max-width: 1200px){.button{font-size:14px;padding:14px 65px}}@media screen and (max-width: 1000px){.button{font-size:12px;padding:12px 60px}}@media screen and (max-width: 650px){.button{font-size:12px;padding:12px 40px}}.button--default{background-color:inherit;border:2px solid #000}.button--stretch{height:52px;width:100%;padding:0}.button--disable{border:1px solid #4e4e4e;color:#4e4e4e;pointer-events:none}.button:hover{background-color:#000;color:#fff}
.banner{display:flex;flex-direction:column;align-items:center;justify-content:space-between;background:#86d3f4;width:19.6%;height:49.5%;padding:20px 10px;position:absolute;top:50%;right:0;box-shadow:-1px 1px 8px 0px rgba(34,60,80,.2);transform:translateY(-50%);animation:slide-in .5s}@media screen and (max-width: 1040px){.banner{height:56%;width:23%}}@media screen and (max-width: 900px){.banner{height:60%}}@media screen and (max-width: 820px){.banner{height:70%;width:27%}}@media screen and (max-width: 720px){.banner{height:100%}}.banner__title{font-weight:500;font-size:16px;line-height:19px;text-align:center}@media screen and (max-width: 1200px){.banner__title{font-size:14px;line-height:17px}}.banner__qrcode{width:126px;height:126px}@media screen and (max-width: 1200px){.banner__qrcode{width:100px;height:100px}}@media screen and (max-width: 1000px){.banner__qrcode{width:80px;height:80px}}@media screen and (max-width: 800px){.banner__qrcode{width:60px;height:60px}}.banner__information{text-align:center;width:126px;font-size:14px;line-height:16px}@media screen and (max-width: 1200px){.banner__information{font-size:12px;line-height:14px}}@keyframes slide-in{0%{transform:translate(100%, -50%)}100%{transform:translate(0, -50%)}}
.video-banner{width:100%;height:100%;position:relative;overflow:hidden}.video-banner__video{width:100%;height:100%}.video-banner__image{width:100%;height:100%;object-fit:cover}.video-banner__stub{position:absolute;top:0;left:0;width:100%;height:100%}
.phone-number-mask{font-weight:bold;font-size:32px;line-height:37px;letter-spacing:2px;white-space:nowrap}
.number-buttons{display:grid;grid-template-columns:repeat(3, 88px);grid-gap:10px;gap:10px}.number-buttons__item{display:flex;justify-content:center;align-items:center;height:52px;background-color:inherit;border:2px solid #000;font-size:16px;line-height:19px;text-transform:uppercase;cursor:pointer;transition:background-color .3s,color .3s}.number-buttons__item--long{grid-column:span 2}.number-buttons__item:hover,.number-buttons__item--active{background-color:#000;color:#fff}
.checkbox{display:flex;align-items:center}.checkbox__input{opacity:0;width:40px;height:40px;flex-shrink:0;cursor:pointer}.checkbox__custom-input{display:flex;justify-content:center;align-items:center;width:40px;height:40px;pointer-events:none;border:2px solid #000;flex-shrink:0;margin-left:-40px}.checkbox__text{font-size:14px;line-height:16px;color:#565656;margin-left:20px}
.number-field{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100%;width:100%;padding:72px 54px}.number-field__title{font-size:26px;line-height:30px;text-align:center}.number-field__information{font-size:14px;line-height:16px;text-align:center}.number-field__error{font-weight:500;color:red}
.sidebar{background-color:#86d3f4;width:380px;height:100%}
.submitted-field{display:flex;flex-direction:column;justify-content:center;width:100%;height:100%;padding:48px}.submitted-field__title{font-weight:bold;font-size:32px;line-height:37px;text-align:center;text-transform:uppercase;letter-spacing:2px;margin:15px}.submitted-field__information{font-size:14px;line-height:16px;text-align:center}
.close-button{display:flex;justify-content:center;align-items:center;background-color:#fff;width:88px;height:52px;border:2px solid #000;position:absolute;top:20px;right:20px;cursor:pointer;transition:background-color .3s,stroke .3s}.close-button:hover,.close-button:hover>svg line{background-color:#000;stroke:#fff}
.qrcode-zone{display:flex;align-items:center;position:absolute;bottom:40px;right:40px}.qrcode-zone__information{font-size:16px;line-height:19px;text-align:right;text-transform:uppercase;color:#fff;width:194px}.qrcode-zone__img{width:110px;height:110px;margin-left:10px}
.phone-number-screen{background-image:url(/smarttv-test/build/static/media/fon.6fd7a8b6.png);background-size:100%;width:100%;height:100%;position:relative}
*{padding:0;margin:0;box-sizing:border-box}body{display:flex;height:100vh;flex-direction:column;justify-content:center;align-items:center;background-color:#000;font-family:Roboto,sans-serif}
